.page-header {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 50px;

  @media (min-width: @desktop-width) {
    height: 100px;
    padding-left: 375px;
    padding-right: 375px;
  }

  &--noJS {
    display: block;
    padding-top: 20px;

    @media (min-width: @desktop-width) {
      display: flex;
      padding-top: 0;
    }
  }
}
