.main-nav {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  background-color: #FFFFFF;
  z-index: 10;
}
.main-nav .main-nav__list {
  display: flex;
  flex-direction: column;
  row-gap: 30px;
  padding-top: 134px;
  height: 568px;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .main-nav .main-nav__list {
    height: 500px;
    box-sizing: border-box;
  }
}
.main-nav .main-nav__item {
  position: relative;
  padding-left: 16px;
  padding-top: 15px;
  padding-bottom: 15px;
  box-sizing: border-box;
  background-color: #FBF8F8;
}
.main-nav .main-nav__item:hover .main-nav__icon-phone {
  color: #FE7865;
}
.main-nav .main-nav__item:hover .main-nav__link {
  color: #FE7865;
}
.main-nav .main-nav__link::after {
  position: absolute;
  content: "";
  width: 10px;
  height: 10px;
  right: 25px;
  top: 45%;
  border: 2px solid rgba(44, 46, 63, 0.5);
  border-left: none;
  border-bottom: none;
  transform: rotate(45deg);
}
.main-nav .main-nav__item:last-child {
  display: none;
}
.main-nav .main-nav__toggle {
  margin: 0;
  padding: 0;
  width: 50px;
  height: 50px;
  box-sizing: border-box;
  border: none;
  position: absolute;
  right: 0;
  background-color: transparent;
}
@media (min-width: 768px) {
  .main-nav .main-nav__toggle {
    width: 70px;
  }
}
.main-nav .main-nav__toggle:hover {
  cursor: pointer;
}
.main-nav .main-nav__toggle::after,
.main-nav .main-nav__toggle::before {
  position: absolute;
  content: "";
  width: 23px;
  height: 1px;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  background-color: #DADADA;
}
.main-nav .main-nav__toggle::after {
  transform: rotate(45deg);
}
.main-nav .main-nav__toggle::before {
  transform: rotate(-45deg);
}
.main-nav--closed {
  position: relative;
  height: 50px;
}
.main-nav--closed .main-nav__list {
  display: none;
}
.main-nav--closed .main-nav__toggle {
  border: none;
  background-color: #FE7865;
}
.main-nav--closed .main-nav__toggle::after {
  display: none;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  transform: rotate(0);
  box-shadow: 0 6px #DADADA, 0 -6px #DADADA;
}
@media (min-width: 1920px) {
  .main-nav {
    position: static;
    height: auto;
    background-color: transparent;
  }
  .main-nav .main-nav__list {
    display: flex;
