    top: 20px;
  }
}
.error-modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 320px;
  height: 444px;
  background-color: #FBF8F8;
  font-size: 25px;
  font-weight: 500;
  line-height: 43px;
  z-index: 10;
  box-sizing: border-box;
  text-align: center;
  color: #48494D;
}
@media (min-width: 768px) {
  .error-modal {
    width: 728px;
    height: 444px;
    padding-left: 161px;
    padding-right: 161px;
  }
}
.error-modal__close-button {
  position: absolute;
  align-self: flex-end;
  right: 20px;
  top: 15px;
  width: 20px;
  height: 20px;
  background-color: transparent;
  border: none;
}
.error-modal__close-button::after,
.error-modal__close-button::before {
  position: absolute;
  content: "";
  width: 25px;
  height: 2px;
  background-color: rgba(44, 46, 63, 0.2);
}
.error-modal__close-button::after {
  transform: rotate(45deg);
}
.error-modal__close-button::before {
  transform: rotate(-45deg);
}
@media (min-width: 768px) {
  .error-modal__close-button {
    right: 30px;
    top: 20px;
  }
}
.hide-block {
